export class InfoAboutHotel {
  constructor(container, data) {
    this.container = container;
    this.data = data;

    console.log(this.container, this.data);

    this._init();
  }

  _init() {
    this.render();
  }

  _createEditBtn(id) {
    const btnEdit = document.createElement("button");

    btnEdit.classList.value = "btn btn-warning mt-auto";
    btnEdit.textContent = "–†–µ–¥–∞–∫—Ç–∏—Ä–æ–≤–∞—Ç—å";
    btnEdit.setAttribute("data-index", id);

    return btnEdit;
  }

  _clear() {
    this.container.innerHTML = "";
  }

  render() {
    this._clear();

    const btnEdit = this._createEditBtn(this.data.id);

    const template = `
        <h5 class='hotel-date'>${this.data.date}</h5>
        <div class='hotel-address'>–ê–¥—Ä–µ—Å: ${this.data.address}</div>
        <div class='hotel-stars'>–ö–æ–ª–∏—á–µ—Å—Ç–≤–æ –∑–≤–µ–∑–¥: ${this.data.stars}</div>
        <div class='hotel-anotherInfo'>–î–æ–ø–æ–ª–Ω–∏—Ç–µ–ª—å–Ω–∞—è –∏–Ω—Ñ–æ—Ä–º–∞—Ü–∏—è: ${this.data.anotherInfo}</div>
    `;

    this.container.innerHTML = this.container.innerHTML + template;
    this.container.append(btnEdit);
  }
}
